/**
 *  
 *  - SCALE:   ,        .
 *  - SCALE_STRETCH:   ,         .
 *  - COVER:   ,        .
 *  - COVER_STRETCH:   ,       .
 * @export
 * @enum {number}
 */
export enum ResizeType {
  SCALE,
  SCALE_STRETCH,
  COVER,
  COVER_STRETCH,
  FIXED,
}

/**
 *  
 * @export
 * @interface ResizeConfig
 */
export interface ResizeConfig {
  /**
   *   width
   * @type {number}
   */
  expectWidth?: number;
  /**
   *   height
   * @type {number}
   */
  expectHeight?: number;
  /**
   *   (jpeg type   quality   )
   * @type {number}
   */
  quality?: number;
  /**
   * canvas   contentType    ,     
   * @type {string}
   */
  expectContentType?: string;
  /**
   * canvas     
   * @type {string}
   */
  fillBgColor?: string;
  /**
   *  
   * @type {ResizeType}
   */
  resizeType?: ResizeType;

  /**
   * exif orientation     
   * @type {boolean}
   */
  applyOrientation?: boolean;
}

/**
 *  
 * @export
 * @interface ResizeResult
 */
export interface ResizeResult {
  /**
   *   Blob
   * @type {Blob}
   */
  blob: Blob;
  /**
   *   width 
   * @type {number}
   */
  width: number;
  /**
   *   height 
   * @type {number}
   */
  height: number;
  /**
   * allowOrientation  exif   orientation 
   * @type {number}
   */
  orientation?: number;
  /**
   *   
   * @type {*}
   * @memberof ResizeResult
   */
  error?: any;
}

/**
 *   
 * @interface DrawBound
 */
export interface DrawBound {
  dx: number;
  dy: number;
  dw: number;
  dh: number;
  mw: number;
  mh: number;
}
