/**
 *   
 *  - CHANGE:  
 *  - RESOLVE: 
 *  - REJECT: 
 * @export
 * @enum {string}
 */
export enum FileReadEventType {
  CHANGE = 'CHANGE',
  RESOLVE = 'RESOLVE',
  REJECT = 'REJECT',
  ABORT = 'ABORT',
}

/**
 *  
 * @export
 * @interface FileReadEvent
 */
export interface FileReadEvent {
  /**
   *  
   * @type {FileReadEventType}
   */
  type: FileReadEventType;
  /**
   *  
   * @type {FileReadState}
   */
  state: FileReadState;
  /**
   * 
   * @type {*}
   */
  error?: any;
}

/**
 *   
 * @export
 * @interface FileReadState
 */
export interface FileReadState {
  /**
   * 
   * @type {string}
   */
  fileName: string;
  /**
   *  (Mime)
   * @type {string}
   */
  fileType: string;
  /**
   *  
   * @type {number}
   */
  fileSize: number;
  /**
   *   
   * @type {number}
   */
  fileLastModified: number;
  /**
   *  
   * @type {number}
   */
  readed: number;
  /**
   *  ( )
   * @type {number}
   */
  total: number;
  /**
   * 1   
   * @type {number}
   */
  chunkSize: number;
  /**
   *     
   * @type {FileReadResponseType}
   */
  responseType: FileReadResponseType;
  /**
   * responseType  BUFFER    ArrayBuffer[]
   * @type {ArrayBuffer[]}
   */
  buffers?: ArrayBuffer[];
  /**
   * responseType  BLOB    Blob
   * @type {Blob}
   */
  blob?: Blob;
}

/**
 *  
 * @export
 * @interface FileReadConfig
 */
export interface FileReadConfig {
  /**
   * 1  
   * @type {number}
   */
  chunkSize?: number;
  /**
   *     
   * @type {FileReadResponseType}
   */
  responseType?: FileReadResponseType;
}

/**
 *       .
 *  - BUFFER: ArrayBuffer[] 
 *  - BLOB: Blob 
 * @export
 * @enum {string}
 */
export enum FileReadResponseType {
  BUFFER = 'BUFFER',
  BLOB = 'BLOB',
}
